<style scoped>
p{
  line-height: 25px;
  padding: 0 25px;
}
ul{
  position: relative;
  width:699px;
  height: 145px;
  overflow: hidden;
  white-space: nowrap;
  padding: 0 10px;
}
li{
  position: absolute;
  display: inline-block;
  width:184px;
  height:145px;
}
.newAutoPlay img{
  width:160px;
  height: 125px;
  margin-left: 12px;
  border:1px solid transparent;
}
.newAutoPlay img:hover{
  border:1px solid #ddd;
}
.newAutoPlay span{
  display: block;
  text-align: center;
  font-size: 14px;
  padding-bottom: 3px;
}
.main{
  padding: 0 30px;
  line-height: 30px;
}
.main>img{
  float: left;
  padding: 5px;
  width:200px;
  height:139px;
  vertical-align: top;
  margin-right: 5px;
}
strong{
  color:red;
}
</style>
<template>
  <div class="">
      <div class="">
        <img :src="indexFirst" alt="" v-if='version === 1'/>
        <img :src="indexFirstEN" alt="" v-if='version === 0'/>
      </div>
      <div class="main" v-if='version === 1'>
          <img :src="companyImage" alt="" />
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<strong>诸暨市驰敏气动元件有限公司</strong>位于“中国南方五金城”——店口，建筑面积一万平方米，注册资金500万元，是一家集研发、制造、销售于一体的气动元件公司。本公司专业生产各种高端黄铜接头，管道配件，水暖配件，快速接头等，广泛用于管道工程项目，如煤气、空调、空气压缩机、气动、制药、消防设备等。
          <br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;我公司拥有整套完整的生产检测设备和一批高素质的专业人员，生产销售气动元件20余年以来，在国内外市场均获很高的知名度。“创新，务实，高效”是我公司秉承的企业宗旨，本公司承诺所有用户一流的产品和最满意的售后，我们期待长久的合作伙伴，共创气动元件行业的美好明天！
      </div>
      <div class="main" v-if='version === 0'>
          <img :src="companyImage" alt="" />
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<strong>Zhuji Chimin pneumatic components CO.</strong>is located in Diankou,in the middle of Zhejiang province,which is called "Hardware City in south China".The company covers ten thousand square meters,it is specialized in producing  pneumatic components,a variety of high-grade brass fittings,pipe fittings, water heating fittings, quick coupling, gas fittings and so on.
          <br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;The company not only have advanced processing equipment, but also equipped with a consummate testing facilities and a number of high-qualified professionals.
          <br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;We can design,develop,and produce various kinds of pneumatic components.Our products are widely used for pipeline engineering projects,such as gas,air conditioning,air compressor,pneumatic,pharmaceutical,fire fighting equipment and so on.
          <br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Adhering to the principle of "quality first",we have more than 20 years experience for producing and operating pneumatic components,the production technology is very mature.Our company promises all users of the first-class products and the most satisfying post-sale service.We look forward to providing you with high quality products,superior customer service and complete satisfaction!
      </div>
      <div class="">
        <img :src="indexSecond" alt="" v-if='version === 1'/>
        <img :src="indexSecondEN" alt="" v-if='version === 0'/>
      </div>
      <div class="newAutoPlay">
        <ul>
          <li v-for="newImage in imgList" class="newAutoPlayLi" @mouseover="stopTimer" @mouseout="startTimer">
            <img :src="newImage.imgUrl" alt="" />
            <span v-if='version === 1'>{{newImage.imgTitle}}</span>
            <span v-if='version === 0'>{{newImage.enName}}</span>
          </li>
        </ul>
      </div>
  </div>
</template>
<script>
import indexFirst from '../assets/images/img/index_first.gif'
import indexFirstEN from '../assets/images/img/index_firstEn.gif'
import indexSecond from '../assets/images/img/index_second.gif'
import indexSecondEN from '../assets/images/img/index_secondEn.gif'
import newImage1 from '../assets/images/newProdcuts/aqf.jpg'
import newImage2 from '../assets/images/newProdcuts/hg.jpg'
import newImage3 from '../assets/images/newProdcuts/ktst.jpg'
import newImage4 from '../assets/images/newProdcuts/knst.jpg'
import newImage5 from '../assets/images/newProdcuts/stbt.jpg'
import newImage6 from '../assets/images/newProdcuts/xsq.jpg'
import companyImage from '../assets/images/img/company.jpg'
import { getVersion } from '../vuex/getters'
export default {
  vuex: {
    getters: {
      version: getVersion
    }
  },
  data () {
    return {
      indexFirst,
      indexFirstEN,
      indexSecond,
      indexSecondEN,
      companyImage,
      imgList: [{
        imgUrl: newImage1,
        imgTitle: '安全阀',
        enName: 'safety-valve'
      }, {
        imgUrl: newImage2,
        imgTitle: '喉箍',
        enName: 'hose clamp'
      }, {
        imgUrl: newImage3,
        imgTitle: '卡套四通',
        enName: 'compression cross'
      }, {
        imgUrl: newImage4,
        imgTitle: '快拧四通',
        enName: 'fast joint cross'
      }, {
        imgUrl: newImage5,
        imgTitle: '四通宝塔',
        enName: 'Hose connector'
      }, {
        imgUrl: newImage6,
        imgTitle: '消声器',
        enName: 'muffler'
      }],
      timer: null
    }
  },
  ready () {
    let imgs = document.getElementsByClassName('newAutoPlayLi')
    for (var i = 0, len = imgs.length; i < len; i++) {
      imgs[i].style.left = i * 184 + 'px'
    }
    this.newProductsDisplay()
  },
  methods: {
    newProductsDisplay: function () {
      let imgs = document.getElementsByClassName('newAutoPlayLi')
      this.timer = setInterval(function () {
        for (var i = 0, len = imgs.length; i < len; i++) {
          imgs[i].style.left = parseInt(imgs[i].style.left) - 1 + 'px'
          if (parseInt(imgs[i].style.left) < -184) {
            imgs[i].style.left = 184 * (len - 1) + 'px'
          }
        }
      }, 10)
    },
    stopTimer: function () {
      clearInterval(this.timer)
    },
    startTimer: function () {
      this.newProductsDisplay()
    }
  }
}
</script>



