<style scoped>
nav{
  padding: 10px 0;
  border-radius: 3px;
  color:#FFF;
  font-weight: 600;
  /*background-color: #749BDE;*/
  border-top: 1px solid black;
  background: linear-gradient(bottom,#163C7D, #ABC4ED);
  background: -webkit-linear-gradient(bottom,#163C7D, #ABC4ED);
  background: -o-linear-gradient(bottom,#163C7D, #ABC4ED);
  background: -moz-linear-gradient(bottom,#163C7D, #ABC4ED);
}
li{
  padding: 3px 0;
  border-right: 1px solid #FFF;
  cursor:pointer;
}
li:last-child{
  border:none
}
.col-20 span{
  cursor: pointer;
  margin-left: 10px;
}
</style>

<template>
  <nav>
    <div class="row" v-if="version === 1">
      <div class="col-50">
        <ul class="row">
          <li class="col-20 text-center" v-link="{path:'/home'}">首&nbsp;&nbsp;页</li>
          <li class="col-20 text-center" v-link="{path:'/instruction'}">公司简介</li>
          <li class="col-20 text-center" id='0' v-link="{path:'/products'}" @click='getProductParentId(0)'>产品展示</li>
          <li class="col-20 text-center" v-link='{path:"/news"}'>公司新闻</li>
          <li class="col-20 text-center" v-link='{path:"/contactus"}'>联系我们</li>
        </ul>
      </div>
      <div class="col-30 text-center">
        {{date}}&nbsp;&nbsp;{{week[new Date().getDay()-1]}}
      </div>
      <div class="col-20 text-right">
        <span @click="changeVersion(1)">中文</span>
        <span>|</span>
        <span @click="changeVersion(0)">English</span>
      </div>
    </div>
    <div class="row" v-if="version === 0">
      <div class="col-70">
        <ul class="row">
          <li class="col-20 text-center" v-link="{path:'/home'}">HOME</li>
          <li class="col-20 text-center" v-link="{path:'/instruction'}">ABOUT US</li>
          <li class="col-20 text-center" id='0' v-link="{path:'/products'}" @click='getProductParentId(0)'>PRODUCT</li>
          <li class="col-20 text-center" v-link='{path:"/news"}'>NEWS</li>
          <li class="col-20 text-center" v-link='{path:"/contactus"}'>CONTACT US</li>
        </ul>
      </div>
      <div class="col-30 text-right">
        <span @click="changeVersion(1)">中文</span>
        <span>|</span>
        <span @click="changeVersion(0)">English</span>
      </div>
    </div>
  </nav>
</template>
<script>
import {
  getProductParentId,
  changeVersion
  } from '../vuex/actions'
import { getVersion } from '../vuex/getters'
export default {
  data () {
    return {
      date: new Date().getFullYear() + '-' + String((new Date().getMonth() + 1) * 101).substr(-2) + '-' + String(new Date().getDay() * 101).substr(-2) + ' ' + new Date().getHours() + ':' + String(new Date().getMinutes() * 101).substr(-2),
      week: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'],
      enWeek: ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN']
    }
  },
  vuex: {
    actions: {
      getProductParentId: getProductParentId,
      changeVersion: changeVersion
    },
    getters: {
      version: getVersion
    }
  }
}
</script>



